<template>
  <div class="top-btn">
    <v-btn class="indigo" fab small @click="goTop">
      <v-icon color="white">mdi-chevron-up</v-icon>
    </v-btn>
  </div>
</template>

<script>
export default {
  methods: {
    goTop () {
      window.scrollBy({
        top: -10000000,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style lang="sass">
.top-btn
  position: fixed
  bottom: 10px
  right: 10px
</style>
